.container{
    width: 100%;
    height: 100vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.left-section{
    width: 75%;
    height: 100vh;
    background-color: white;
}
.right-section{
    width: 25%;
    height: 100vh;
    background-color: pink;
}
.header-nav{
    width: 95%;
    height: 10vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-inline: 1rem;
    /* box-shadow: 2px 2px 3px 4px #ccc; */
}

.header-nav img{
    width: 18%;
}
.body{
    width: 100%;
    height: 75vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.body .localVideo{
    width: 95%;
    height: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    position: absolute;

}
.body .remoteVideos{
    width: 25%;
    height: 95%;
    position: absolute;
    z-index: 1;
    right: 1rem;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}
.body .remoteVideos .remoteVideoBox{
    width: 80%;
    height: 8rem;
    border-radius: 10px;
    background-color: transparent;
}
.footer{
    width: 100%;
    height: 15vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.vid{
    width: 50% !important;
    height: 100% !important;
    object-fit: cover !important;
    border-radius: 10px !important;
}
