.container{
    width: 100%;
    height: 100vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.joinCard,.createForm,.joinForm{
    width: 30%;
    padding: 10px;
    border-radius: 10px;
    background-color:#fff;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    text-align: center;
}
.joinCard h3{
    font-size: 20px;
    font-weight: 700;
    color: black;
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
}
.header button{
    border: none;
    background-color: transparent;
    font-size: 30px;
    cursor: pointer;
    margin-right: 7.5rem;
}